import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import { BtnSearch, BtnSave } from 'components/common/button';

const { Option } = Select;

@Form.create({ name: 'trainingPlan' })
class SearchBarTrainingPlan extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" className="ant-advanced-search-form" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="Hệ">
              {getFieldDecorator('system1', {})(
                <Select>
                  <Option value="1">Đại học chính quy</Option>
                  <Option value="2">Cao đẳng chính quy</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Khoa">
              {getFieldDecorator('system2', {})(
                <Select>
                  <Option value="1">Cơ-điện tử</Option>
                  <Option value="2">Quản trị kinh doanh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Khóa">
              {getFieldDecorator('system3', {})(
                <Select>
                  <Option value="1">Khóa 1</Option>
                  <Option value="2">Khóa 2</Option>
                  <Option value="3">Khóa 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="CN">
              {getFieldDecorator('system4', {})(
                <Select>
                  <Option value="1">Cơ khí</Option>
                  <Option value="2">Điện tử viễn thông</Option>
                  <Option value="3">Điện công nghiệp</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Lớp">
              {getFieldDecorator('system5', {})(
                <Select>
                  <Option value="1">KT1</Option>
                  <Option value="2">KT2</Option>
                  <Option value="3">KT3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Học kỳ">
              {getFieldDecorator('system6', {})(
                <Select>
                  <Option value="1">Học kỳ 1</Option>
                  <Option value="2">Học kỳ 2</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={12} style={{ textAlign: 'right', marginTop: '21px' }}>
            <BtnSearch />
            <BtnSave title="Gán lớp học chung" style={{ marginRight: 0 }} />
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBarTrainingPlan;



