import React, { Component } from 'react';

import { Card, Row, Table, Col } from 'antd';

import Page from 'components/LayoutComponents/Page';
import { TransplantLearning } from 'dump/data';
import './style.scss';
import SearchBarTrainingPlan from './FormTransplantLearning';

class TransplantLearningTable extends Component {
  state = {};

  render() {
    const columns = [
      {
        title: 'Tên lớp',
        dataIndex: 'className',
      },
      {
        title: 'Tên môn',
        dataIndex: 'name',
      },
      {
        title: 'Số học trình',
        dataIndex: 'SHT',
      },
      {
        title: 'Lớp ghép',
        dataIndex: 'classG',
      },
    ];
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
    };
    return (
      <Card title="Gán lớp học ghép">
        <Row>
          <Col span={24}>
            <SearchBarTrainingPlan />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              rowSelection={rowSelection}
              bordered
              dataSource={TransplantLearning}
              columns={columns}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TransplantLearningTable />
  </Page>
);



